body {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

.dashboard-page {
  padding-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* overflow-x: hidden; */
  height: 100vh;
}

.content-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  padding-bottom: 12rem;
}

.about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  justify-content: center;
  text-align: center;
}

.about-col {
  text-align: center;
  font-family: "Fredoka One", sans-serif;
  color: rgb(40, 40, 40);
  font-weight: 800;
  font-size: x-large;
}

.title {
  font-family: "Fredoka One", sans-serif;
  color: rgb(40, 40, 40);
  font-weight: 900;
  font-size: xx-large;
  /* text-align: center; */
  margin-top: 2rem;
}

.description {
  font-family: "Fredoka One";
  color: rgb(40, 40, 40);
}

.description1 {
  font-family: "Fredoka One";
  color: rgb(40, 40, 40);
  font-weight: 300;
  padding-top: 5rem;
  padding-left: 5rem;
  padding-right: 5rem;
}

.content-row {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
}

.content-col {
  flex: 0 0 70%;
}

.logo-col {
  padding-top: 5rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 0 0 30%;
}

.logo {
  width: 100%;
  max-width: 450px;
  /* animation: floatAnimation 4s infinite; */
  padding-right: 5rem;
}

.content {
  text-align: left;
  padding-top: 5rem;
  padding-left: 5rem;
  padding-right: 15rem;
}

.custom-button {
  border: 2px solid #3b5998;
  background-color: transparent;
  color: #3b5998;
  font-family: "Fredoka One", sans-serif;
  font-size: 1rem;
  font-weight: bold;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.custom-button:hover {
  background-color: #3b5998;
  color: #fff;
}

/* LOGO ANIMATION */

@keyframes floatAnimation {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0);
  }
}

