.shake {
  animation: shakeAnimation 0.5s;
}

@keyframes shakeAnimation {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
  75% {
    transform: translateX(-5px);
  }
  100% {
    transform: translateX(0);
  }
}

.gallery-image {
  /* Existing styles */

  transition: transform 0.3s;
}

.gallery-image:hover {
  transform: translateX(-2px);
  animation: shakeAnimation 0.4s infinite;
}

@keyframes shakeAnimation {
  0% {
    transform: translateX(-2px);
  }
  50% {
    transform: translateX(2px);
  }
  100% {
    transform: translateX(-2px);
  }
}

